export default function FeatureSection() {
    return (
        <section className="flex flex-row w-full gap-8 p-8">
            {/* LEFT: Featured Large (2/3 width) */}
            <div className="flex-[2] bg-red-400 rounded-lg flex items-center justify-center h-[480px] text-white text-3xl font-bold">
                FEATURED LARGE
            </div>

            {/* RIGHT: 6 smaller items (1/3 width, 2Ã—3 grid) */}
            <div className="flex-[1] grid grid-cols-2 gap-4">
                {Array.from({ length: 6 }).map((_, i) => (
                    <div
                        key={i}
                        className="bg-blue-400 h-[150px] flex items-center justify-center text-white text-lg font-semibold rounded-md"
                    >
                        SMALL {i + 1}
                    </div>
                ))}
            </div>
        </section>
    );
}
